import { Button, Card, CardGroup, Form, Modal, PinCode, Tag, Toast, Typography } from '@douyinfe/semi-ui'
import './resource.css'
import React, { useEffect } from 'react';
import axios from 'axios';
import { use } from 'react';
const { Paragraph, Title, Numeral } = Typography;
const Captcha = () => {
    return <></>
}
const forbid = () => {
    localStorage.setItem('forbid', new Date().getTime())
}
const isForbid = () => {
    const forbidTime = parseInt(localStorage.getItem('forbid'))
    if (forbidTime) {
        const now = new Date().getTime()
        const forbidDate = new Date(forbidTime);
        const diff = (now - forbidDate.getTime()) / 1000;
        if (diff < 60 * 60 * 24) {
            return true;
        } else {
            localStorage.removeItem('forbid')
            return false;
        }
    } else {
        return false;
    }
}


const DownLoadForm = ({ id, open, onCancel }) => {
    const [times, setTimes] = React.useState(isForbid() ? 4 : 0)
    const [value, setValue] = React.useState('')
    const [disabled, setDisabled] = React.useState(isForbid())
    const ref = React.useRef(null)
    // const [open, setOpen] = React.useState(false)
    useEffect(() => {
        setDisabled(isForbid())
        setTimes(isForbid() ? 4 : 0)

    }, [open])
    const download = () => {
        if (times >= 4 || isForbid()) {
            Toast.error('')
            setDisabled(true)
            if (times >= 4) forbid()
            return;

        }
        if (value.length < 6) {
            Toast.error('')
            return;
        }
        validatePwd(id, value).then(data => {
            // console.log(data);
            if (data.length === 0) {
                Toast.error('')
                setValue('')
                console.log(ref);
                setTimes(times + 1)
                ref.current.focus(0)
                return;
            } else {
                let filename = data.filename;
                let url = data.url;
                const a = document.createElement('a');
                a.href = url;
                a.download = filename;
                a.click();
                onCancel();
                setValue('')
                Toast.success('')
            }

        })

    }

    return (<Modal
        centered
        visible={open}
        onCancel={onCancel}
        title={'(ID:' + id + ')'}
        footer={null}
    >
        <div className='download-form'>
            <PinCode
                format={'mixed'}
                value={value}
                onChange={v => setValue(v)}
                size='large'
                onComplete={download}
                ref={ref}
                disabled={disabled}
            />
            {/* <div className='download-btn'>
                <Button
                    type='primary' style={{ margin: '10px' }}>
                    
                </Button>
            </div> */}
            {
                times >= 4 &&
                <div style={{ margin: '10px' }}>
                    <Typography.Text type='tertiary' >24</Typography.Text>

                </div>
            }
        </div>
    </Modal>)
}


function getResourceList() {
    return axios.get("/list").then(res => res.data).then(data => {
        // console.log(data);
        return data;
    })
}


const ResourceDetail = ({ r, wantDownload }) => {

    const record = JSON.parse(JSON.stringify(r));
    const description = record.fields.description;
    const name = record.fields.name;
    const title = record.fields.title;
    const createdTime = record.createdTime;
    const platForms = record.fields.tags;
    const id = record.id;


    return (<>

        <Card
            title={title}
            shadows='hover'
            style={{ width: "500px", borderRadius: '10px', padding: '10px', margin: '10px' }}
            onClick={() => wantDownload(id)}
        >
            <Typography.Title heading={4} className='heading'>{name}</Typography.Title>
            <br />
            <Paragraph>
                <Typography.Text type='tertiary'>{description}</Typography.Text>
            </Paragraph>
            <div style={{ marginTop: '10px' }}>
                {
                    platForms.map(item => <Tag
                        color='cyan'
                        size='large'
                        shape='circle'
                        style={{ marginLeft: '5px' }}
                        type='ghost'
                        key={item}
                    >{item}</Tag>)
                }

            </div>

            <div style={{ display: 'flex', justifyContent: 'space-between', margin: '10px 0' }}>

                <div className="time">
                    {createdTime}
                </div>
            </div>
        </Card >
    </>)
}
function validatePwd(id, pwd) {
    return axios.post("/download/" + id, {
        pwd: pwd
    }).then(data => data.data.records).then(data => {
        // console.log(data);

        return data.length !== 0 ? data[0].fields.attachments[0] : [];
    })
}

export default function ResourceShow() {
    const [url, setUrl] = React.useState('')
    const [file_info, setFileInfo] = React.useState({
        file_name: '',
        size: '',
    })

    const [records, setRecords] = React.useState([])

    const [dId, setDId] = React.useState('')
    const [open, setOpen] = React.useState(false)




    useEffect(() => {
        getResourceList().then(data => {
            // console.log(data.records);
            setRecords(data.records)
        })
    }, [])


    return <div>
        <DownLoadForm
            url={url} name={file_info.file_name}
            id={dId}
            open={open}
            onCancel={() => setOpen(false)}
        />
        <CardGroup spacing={12}>
            {records.map(record => <ResourceDetail
                key={record.id}
                r={record}
                wantDownload={(id) => { setDId(id); setOpen(true) }}
            />)}
        </CardGroup>
    </div>;
}